.transactionHistory {
  margin: 0 auto;
  width: 400px;
  border: 1px solid black;
}
tr {
  height: 50px;
  font-size: large;
}
thead {
  background: slateblue;
  color: lightyellow;
}
tbody tr:nth-child(2n) {
  background: darkkhaki;
}
tbody tr:nth-child(2n + 1) {
  background: lightpink;
}
